<template>
  <div class="verify-token">
      <div v-if="loading" class="loading">
            <h3 v-if="loading">Verifying your account...</h3>
          <p>Please wait a seconds</p>
      </div>
      <div v-if="error" class="verify-error">
        <h3> {{ error }}</h3>
      </div>
      <div v-if="success" class="verify-success" >
          <h3> Success Verify Account..</h3>
          <h5>Back to home in {{ countdown }}</h5>
      </div>
  </div>
</template>

<script>
import { verifyToken,addVisitor } from "@/utils/index"
import { mapActions } from "vuex"

export default {
    name : 'verifyToken',
    data() {
        return{
            loading : false,
            error : "",
            success : false,
            countdown : 5,
        }
    },
    async created() {
        const token = this.$route.params.token || null;
        if(!token) return this.$router.push({ name :  'error'});
        await this.sendToken(token);
    },
    methods : {
        ...mapActions(['userAuth']),
        async sendToken(token) {
            this.loading = true;
            try {
                await verifyToken(token);
                this.loading = false;
                this.success = true;
                setInterval(() => {
                    this.countdown--;
                },1000)
                setTimeout(async () =>  {
                    await addVisitor();
                    this.$router.push({
                        name : 'home',
                    });
                },this.countdown * 1000 );
            } catch({message}) {
                this.loading = false;
                this.error = message;
            }
        },
    }
}
</script>

<style  lang="scss" scoped>

@import "@/assets/scss/_variables.scss";

.verify-token {
    margin-top: 3rem;
    display : flex;
    flex-direction: column;
    justify-content : center;
    font-family : $poppins;
    padding : 2rem;
    width : 100%;
    height : 70%;
    align-items: center;

    .verify-error {
        color :red
    }
    
    .verify-success {
        color : #0a7c00;

        h5 {
            color : black;
        }
    }
}

</style>